import { Link } from 'react-router-dom';
import { useParams } from "react-router-dom";
import parse from "html-react-parser";
import { MapContainer, TileLayer, GeoJSON } from 'react-leaflet';
import Logo from './flook.svg';
import Data from './data/posts.json';
import MapData from './data/maritimes.json';
import MarUnion from './data/marunion.json';
import 'materialize-css/dist/css/materialize.min.css';
import './App.css';

const Homepage = () => {
	return (
		<div>
				<Header/>
        <Banner/>
        <Postlist/>
		</div>
	)
};

const Header = () => {
	return (
    <div className='header'>
			<Link to='/'><img src={Logo} alt='FORELOOKER'/></Link>
    </div>
	)
};

const Banner = () => {
	return (
		<div>
    <div className='banner'>
          <span className="new badge red" data-badge-caption="#whatif"></span>
					<span className="new badge red" data-badge-caption="#alternatehistory"></span>
    </div>
		<hr className='tophr'/>
		</div>
	)
};

const Postlist = () => {
	return (
    <div className='mymainpage row'>
      { Data.map((postdetail,i)=>{
        return <div className='col s12 l4'>
                 <Link to={`/post/${postdetail.id}`}>
								 <div className='card'>
                   <div className="card-image">
                     <img src={postdetail.image} alt='{postdetail.id}'/>
                   </div>
										<div className="card-content">
											 <h6 className='cardtitle'>{parse(postdetail.name)}</h6>
											 <p className='maincard'>{parse(postdetail.desc)}</p>
										</div>
                 </div>
								 </Link>
               </div>
          })}
    </div>
	)
};

function Checkpost() {
  const { id } = useParams();
  let num = Object.values({id})[0];
  const mypost = Data[num];
  return (
    <div>
	    <Header/>
			<Banner/>
	    <div className='mycontent'>
					<nav className='white z-depth-0 hide-on-med-and-down'>
							{mypost.sections.map((parts,j)=> (
								<Checkcrumb key={j + 'crumb'} crumbtext={parts.title} />
							))}
					</nav>
	        <h4>{parse(mypost.name)}</h4>
	        {mypost.sections.map((parts,j)=> (
	          <div key={j + 'content'}>
	            <h5 id={parts.title}>{parse(parts.title)} </h5>
	            <Checkpic  pictext={parts.image} />
	            <p className='content'>{parse(parts.content)}</p>
							<Checkmap  maptext={parts.map}/>
	            <Checklink  linktext={parts}/>
	          </div>
	        ))}
	    </div>
    </div>
  )
}

function Checkcrumb(crumbtext) {
  let crumb = crumbtext.crumbtext;
  if (crumb !== "") {
		let myid = "#" + crumb;
    return <a href={myid} className="breadcrumb">{crumb}</a>;
  }
  return "";
}

function Checkpic(pictext) {
  let pic = pictext.pictext;
  if (pic !== "") {
    return <img src={pic} width='100%' alt='pictext.pictext'/>;
  }
  return "";
}

function style(curmap) {
	return {
    fillColor: curmap.properties.fillColor,
		color: curmap.properties.stroke,
		weight: .75
  };
}

function onEachFeature(feature, curmap) {
    if (feature.properties) {
        curmap.bindPopup(feature.properties.name);
    }
}

function Checkmap(maptext) {
	let map = maptext.maptext.split(';');
  if (map.length > 1) {
		let maps = [MapData,MarUnion]; // this array are all the map geojsons
		let latlng = [map[0], map[1]];
		let zlevel = map[2];
		const msg = map[3];
		let curmap = maps[map[4]].features;

		if (msg !== "null") {
			return (
				<div>
					<MapContainer center={latlng} zoom={zlevel} scrollWheelZoom={false}>
						<TileLayer url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'/>
						<GeoJSON data={curmap} style={style} onEachFeature={onEachFeature}/>
					</MapContainer>
				</div>
			)
		} else {
			return (
				<div>
					<MapContainer center={latlng} zoom={zlevel} scrollWheelZoom={false}>
						<TileLayer url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'/>
						<GeoJSON data={curmap} style={style}/>
					</MapContainer>
				</div>
			)
		}
	}
  return "";
}

function Checklink(linktext) {
  let link = linktext.linktext.link;
	let top = linktext.linktext.hr;
  if (link !== "" && top !== "") {
    return <div><a href={link} target='_blank' rel='noreferrer'>More info</a> / <a href='#top'>Top</a><hr className='myhr'/></div>;
  }
	if (link === "" && top !== "") {
		return <div><a href='#top'>Top</a><hr className='myhr'/></div>;
	}

  return "";
}

export {Homepage, Checkpost};
